.greyTextBox {
  background-color: rgb(236, 236, 236);
  outline: 15px solid rgb(236, 236, 236);
  border-radius: 1px;
  margin-bottom: 50px;
  margin-top: 60px;
}

.ticorporateTextContainer {
  margin: 110px 0px 50px;
}

.ticorporateTextContainer p {
  margin-top: 22px;
}

.ticorporateTextContainer li {
  font-size: 18px;
  margin-bottom: 18px;
  padding-left: 1em;
  text-indent: -1em;
}

.ticorporateTextContainer ul {
  list-style: none;
  margin-left: 0;
  padding-left: 0;
}

.starBullet:before {
  content: '✦';
  padding-right: 10px;
}

.imageCarousel {
  width: 70%;
  max-width: 1000px;
  margin: auto;
  height: fit-content;
  margin: 20px auto;
}

.imageCarousel img {
  position: relative;
  z-index: -10;
}

.imageCarousel li:before {
  content: none;
}

.plusBullet:before {
  content: '+';
  padding-right: 10px;
  color: green;
  font-weight: bolder;
}

.minusBullet:before {
  content: '-';
  padding-right: 10px;
  color: red;
  font-weight: bolder;
}

.textAndGifContainer {
  padding: 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.textAndGifContainerText {
  padding-right: 15px;
}

.textAndGifContainer h4 {
  margin-top: 0px;
}

.zenhubTaskImage {
  width: 90%;
  max-width: 500px;
  border: 2px solid rgb(230, 230, 230);
  border-radius: 5px;
}

.comparisonImage {
  width: 80%;
  max-width: 600px;
  border: 7px solid #ececec;
  border-radius: 5px;
}

@media screen and (max-width: 820px) {
  .imageCarousel {
    width: 100%;
  }

  .textAndGifContainer {
    flex-direction: column;
    align-items: center;
  }
}
