.navigationBar {
  background-color: white;
  color: rgb(48, 48, 48);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  border-bottom: 2px solid rgb(240, 240, 240);
}

.navigationBarContent {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0px auto 0px auto;
  width: 90%;
  max-width: 1000px;
  white-space: nowrap;
}

h1 {
  color: rgb(46, 46, 46);
}

h1:hover {
  color: #000000;
}

.links a {
  margin-left: 24px;
  color: rgb(20, 20, 20);
}

.links a:hover {
  color: #df8c3e;
}

.sideMenuIcon {
  display: none;
}

.sideMenuIcon svg {
  height: 50px;
  width: 50px;
  cursor: pointer;
}

@media screen and (max-width: 800px) {
  .links {
    display: none;
  }

  .sideMenuIcon {
    display: initial;
  }
}
