.textAndGraphics {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 50px;
}

.wideGraphicsContainer {
  position: relative;
  width: 380px;
  top: 22px;
}

.mobileGraphicsContainer {
  display: none;
}

.frontPageImage {
  width: 320px;
  height: 320px;
  border-radius: 100%;
  margin-left: 20px;
}

.circleContainer {
  top: 0;
  left: 0;
  width: 100%;
}

.circle {
  border-radius: 100%;
}

.circle:nth-child(1) {
  background-color: #ffb67260;
  width: 270px;
  height: 270px;
  position: absolute;
  left: 80px;
  top: 180px;
  z-index: -1;
}

.circle:nth-child(2) {
  background-color: #dfe0df90;
  width: 230px;
  height: 230px;
  position: absolute;
  left: 60px;
  top: 340px;
  z-index: -2;
}

.circle:nth-child(3) {
  background-color: #3abeaa40;
  width: 200px;
  height: 200px;
  position: absolute;
  left: 140px;
  top: 500px;
  z-index: -3;
}

.circle:nth-child(4) {
  background-color: #ffb67260;
  width: 100px;
  height: 100px;
  position: absolute;
  left: 100px;
  top: 650px;
  z-index: -4;
}

.circle:nth-child(5) {
  background-color: #3abeaa40;
  width: 100px;
  height: 100px;
  position: absolute;
  left: 100px;
  top: 650px;
  z-index: -4;
  display: none;
}

@media screen and (max-width: 750px) {
  .wideGraphicsContainer {
    display: none;
  }

  .mobileGraphicsContainer {
    display: initial;
  }

  .mobileGraphicsContainer img {
    margin: auto;
    position: relative;
    left: 5px;
  }

  .circle:nth-child(1) {
    left: 235px;
    top: 140px;
    background-color: #dfe0df90;
  }

  .circle:nth-child(2) {
    left: -20px;
    top: 190px;
    background-color: #ffb67260;
  }

  .circle:nth-child(3) {
    left: 425px;
    top: 220px;
    background-color: #3abeaa40;
  }

  .circle:nth-child(4) {
    left: 590px;
    top: 220px;
  }

  .circle:nth-child(5) {
    display: initial;
    left: 25px;
    top: 130px;
  }
}
