.sideMenu {
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  width: 100%;
  background-color: rgb(236, 236, 236);
  z-index: 9;
}

.sideMenuContent {
  width: 98%;
  margin: 80px auto 0px auto;
}

.sideMenuLinks {
  display: flex;
  flex-direction: column;
}

.sideMenuLinks a {
  padding: 25px 10px 20px 20px;
  margin-bottom: 10px;
  color: black;
  text-decoration: none;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.sideMenuLinks a:hover {
  color: #df8c3e;
}

@media screen and (min-width: 820px) {
  .sideMenu {
    display: none;
  }
}
